<template>
    <div>
        <Content :kinds="kinds" :pushProduct="pushProduct"></Content>
        <Footer :shoppingCart="shoppingCart" />
    </div>
</template>

<script type='text/ecmascript-6'>
    import Content from './body/Content'
    import Footer from './body/Footer'
    import {
        getProducts
    } from '@/api/products'
    export default {
        components: {
            Content,
            Footer,
        },
        mounted() {
            getProducts().then(({
                code,
                data
            }) => {
                if (code === 200) {
                    this.kinds.push({
                        kind: '',
                        products: data["commodity"]
                    })
                    this.kinds.push({
                        kind: '',
                        products: data["discount"]
                    })
                }
            })
        },
        data() {
            return {
                headerHeight: "auto",
                kinds: [],
                shoppingCart: [],
            }
        },
        methods: {
            pushProduct(product) {
                var index = -1;
                this.shoppingCart.forEach((item, idx) => {
                    if (item.product === product) {
                        item.count++;
                        index = idx
                    };
                })
                if (index === -1) {
                    this.shoppingCart.push({
                        count: 1,
                        product
                    })
                }
            }
        }
    }
</script>

<style lang='scss' type='text/css'>
    html,
    body {
        height: 100%;
        width: 100%;
        padding: 0;
        margin: 0;
    }
    body {
        overflow: hidden;
    }
</style>