<template>
    <div class="store__content">
        <ListSliderContent :style="{height:  sliderHeight, top: sliderTop}" class="list__kinds" :kinds="kinds"/>
        <vuescroll :style="{height: height+'px'}" :ops="ops" @handle-scroll="handleScroll">
            <Header class="header" ref="header" ></Header>
            <ListProducts class="list__products" v-for="(item,key) in kinds" :key="key" :kind="item" :pushProduct="pushProduct"/>
        </vuescroll>
    </div>
</template>

<script type='text/ecmascript-6'>
    import vuescroll from 'vuescroll'
    import ListSliderContent from './Content/ListSliderContent'
    import ListProducts from './Content/ListProducts'
    import Header from './Header'
    export default {
        props: [
           "kinds",
           "pushProduct"
        ],
        components: {
            Header,
            ListProducts,
            ListSliderContent,
            vuescroll
        },
        mounted() {
            let h = 0,
                value = this.$refs["header"] ? this.$refs["header"].$el.clientHeight : 0;
            this.height = document.body["offsetHeight"] - 50;
            if (value) {
                this.headerHeight = value;
            }
        },
        computed: {
            sliderHeight() {
                if (this.scrollTop >= this.headerHeight) {
                    return this.height + 'px';
                }
                return this.height - this.headerHeight + this.scrollTop + 'px'
            },
            sliderTop(value) {
                if (this.scrollTop >= this.headerHeight) {
                    return '0px'
                }
                return this.headerHeight - this.scrollTop + 'px'
            }
        },
        data() {
            return {
                height: 0,
                headerHeight: 0,
                scrollTop: 0,
                ops: {
                    sizeStrategy: 100,
                    vuescroll: {
                        mode: 'native',
                        sizeStrategy: 'percent',
                        detectResize: true
                    },
                    scrollPanel: {},
                    rail: {},
                    bar: {}
                },
            }
        },
        methods: {
            handleScroll(vertical) {
                this.scrollTop = vertical.scrollTop;
            }
        }
    }
</script>

<style lang='scss' scoped='' type='text/css'>
    .store__content {
        position: relative;
        border-bottom: 1px solid gainsboro;
        box-sizing: border-box;
        padding-bottom: 2px;
        .list__products {
            margin-left: 40px;
        }
        .list__kinds {
            position: absolute;
            top: 0;
            left: 0;
            width: 40px;
            z-index: 2;
            overflow: scroll;
            background: rgba(8, 8, 8, .1);
        }
    }
</style>